	module.exports = {"data":{"markdownRemark":{"html":"<p><strong><em>Tourist Kid</em></strong><br>\n<em>Crude Tracer</em><br>\nReally cool and immersive sound bathing record, but almost always only two layers to each track. Background: mid-weight ambient drapery from a synthetic pad and chorus blend. Foreground: gnat-cloud gusts of jittery sampling. And now that I'm thinking about it, the choral voices stake a deep root through ambient music to Music For Airport and deeper into choral church music.</p>\n<p><strong><em>Your Planet is Next</em></strong><br>\n<em>Laid Back EP</em><br>\nLacks in dynamic structure but 3/4 tracks get revved up on cheery spunk. Pro forma machine funk that's tilted punkwise left of center, like Helena Hauff, but smiley, not glowery. Really like the B1, which leads with a scanty acid melody and an unadultered Amen breakloop filling in the gaps between a 4/4 bass + hi-hat pattern. </p>\n<p><strong><em>Stan Getz and Charlie Byrd</em></strong><br>\n<em>Jazz Samba</em><br>\nAll voices perfectly placed here. Light but (super) essential framework from the rhythm section. Stan Getz sometimes overriding Charlie Byrd, but mostly ceding space where the riffs need to tear through. Tenor sax is the perfect instrument for samba jazz because it carries so much air from the reed through the bell. It's the voice of the summer breeze.</p>","frontmatter":{"date":"June 11, 2018","path":"/early-june","title":"Tourist Kid, Your Planet is Next, Getz + Byrd"}}},"pathContext":{}}

